html {
	background-color: #5f45bb;
	background-image: linear-gradient(to bottom right, #180cac, #d054e4);
	color: #fff;
	font-family: "Quicksand", sans-serif;
	font-size: 16px;
	-moz-osx-font-smoothing: grayscale;
	-webkit-font-smoothing: antialiased;
	line-height: 1.5;
	min-height: 100vh;
	min-width: 300px;
	overflow-x: hidden;
	text-shadow: 0 3px 5px rgba(0, 0, 0, 0.1);
}

body {
	margin: 0;
}

a {
	color: currentColor;
	cursor: pointer;
	text-decoration: none;
}

.picture-image  {
	border: 4px solid black;
}

/* 2. shared */

.wallpaper,
.picture-shadow,
.picture-image {
	display: block;
	height: 100%;
	left: 0;
	top: 0;
	width: 100%;
}

time {
	color: white;
        background-color: black;
        padding: 6px 10px 6px 10px;
        border-radius: 8px;
}

.tag {
	color: black;
        background-color: cyan;
        padding: 6px 10px 6px 10px;
        border-radius: 8px;
        font-weight: 900;	    
	font-size: .8rem;
}

.post-content h2 {
	font-size: 2rem;
        font-weight: 900;    
}

.job,
.button {
	font-family: 'Lilita One', cursive;
	letter-spacing: 0.3rem;
	text-transform: uppercase;
}

.button,
.social a {
	transform-origin: center;
	transform-duration: 100ms;
}

.social {
	display: flex;
	margin-top: 0rem;
	margin-bottom: 1.5rem;
	padding-left: 0px;
}

.social li {
	height: 2rem;
	margin-right: 0.5rem;
	text-align: center;
	width: 2rem;
	text-decoration: none;
	list-style-type: none;
}

.social a {
	align-items: center;
	display: flex;
	font-size: 1.5rem;
	height: 2rem;
	justify-content: center;
	opacity: 0.5;
	transition-property: opacity, transform;
	width: 2rem;
	will-change: opacity, transform;
}


.social a {
	align-items: center;
	display: flex;
	font-size: 1.5rem;
	height: 2rem;
	justify-content: center;
	opacity: 0.5;
	transition-property: opacity, transform;
	width: 2rem;
	will-change: opacity, transform;
}

.social a:hover {
	opacity: 1;
	transform: scale(1.25);
}

.social a:active {
	opacity: 1;
	transform: scale(1.1);
}


/* 3. specific */

.wallpaper {
	background-image: url('../assets/background2.jpeg');
	background-position: center;
	background-size: cover;
	opacity: 0.2;
	position: fixed;
	z-index: -10;
}


.side {
	max-height: 20rem;
	max-width: 20rem;
}

.about {
	max-width: 26rem;
}

.wrapper-main {
	display: flex;
	position: relative;
	max-width: $content-width;
}

.picture {
	padding-top: 100%;
	position: relative;
	width: 100%;
	margin: 0;

}

.picture-shadow {
	border-radius: 290588px;
	background-image: radial-gradient(#000 0%, rgba(0, 0, 0, 0) 70%);
	position: absolute;
	top: 10%;
}

.picture-image {
	border-radius:290588px; 
	position: absolute;
}


.name {
	font-size: 2.25rem;
	line-height: 1.125;
	margin-bottom: 0.5rem;
}

.job {
	color: #ffe479;
	font-size: 0.75rem;
}

.hr {
	background-color: #ff009e;
	border: none;
	content: "";
	height: 1px;
	margin-bottom: 1.5rem;
	margin-top: 1.5rem;
	transform-origin: center left;
	width: 100%;
}

.description {
	font-size: 1.5rem;
}

.contact {
	display: inline-block;
	margin-top: 1.5rem;
	vertical-align: top;
}

.button {
	background-color: #fff;
	border-radius: 902575px;
	box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.2);
	color: #9013fe;
	display: inline-block;
	font-size: 0.875rem;
	line-height: 1;
	padding: 1.25rem 2em;
	text-shadow: none;
	transition-property: box-shadown, transform;
	user-select: none;
	vertical-align: top;
	white-space: nowrap;
	will-change: box-shadow, transform;
}

.button:hover {
	box-shadow: 0 1.5rem 3rem rgba(0,0,0,0.2);
	transform: scale(1.02) translateY(-4px);
}

.button:active {
	box-shadow: 0 0.5rem rgba(0,0,0,0.3);
	transform: scale(0.98) translateY(-2px);
}



/* 4. Responsiveness */

@media screen and (max-width: 999px) {
	.wrapper-main {
		flex-direction: column;
		justify-content: center;
		align-items: center;
		padding: 1rem; 
	}

	.side {
		margin-bottom: 2rem;
		width: 100%;
		margin-top: 2rem;
	}
	
}


@media screen and (min-width: $content-width) {
	.wrapper-main {
		align-items: center;
		justify-content: space-around;
		justify-content: space-evenly;
		padding: 1rem;
	}

	.side {
		flex-grow: 0;
		flex-shrink: 0;
		height: 20rem;
		margin-left: 4rem;
		order: 2;
		width: 20rem;
	}

	.about {
		flex-grow: 1;
		flex-shrink: 1;
	}
	
}
